// header ======================================

.header-top {
	padding: 33px 0;
		&__row {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		&__left {
			display: flex;
			gap: 30px;

		}

		&__logo {
			border-right: 3px solid rgba(196, 196, 196, 0.32);
			padding: 0 30px 0 0;
		}


		&__right {
			display: flex;
			align-items: center;
			gap: 35px;
		}

		&__phone {
			display: flex;
			align-items: center;
			a{
				line-height: 22px;
				font-weight: 800;
				color: #2E231B;
				display: flex;
				gap: 8px;
			}
			img{
				display: inline-block;
			}

		}


}

.header-menu {
	background: #EAEAEA;
	position: fixed;
	width: 100%;
	height: 53px;
	z-index: 100;
		&__list {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 15px 0;

			a{
				font-weight: 700;
				line-height: 22px;
				color: $text;
				display: block;
				position: relative;
				transition: all 0.3s ease 0s;
				&:hover{
					color: $main-color;
					&::after{
						background: $gradient;
						}
				}
				&::after{
					content: '';
					height: 2px;
					width: 43px;
					position: absolute;
					bottom: -6px;
					left: 0;
					background: #878787;
					display: block;
					transition: all 0.3s ease 0s;
				}
				
			}
			 li:last-child {
				
				a{
					display: flex;
					gap: 7px;
					&::after{
						display: none;
					}
					&::before{
						content: '';
						width: 30px;
						height: 20px;
						display: block;

						background: url(../img/icon/Person_room.svg) 100% no-repeat;
					}
				}
				
			}
		}
}
.header-scrolled{
	.header-menu{
		top: 0;
		height: 70px;
		align-items: center;
		padding: 10px 0px 0px 0px;	
		box-shadow: 0 6px 10px #fff;
	}
}

// slider ============================================
.main-slider {
	.swiper-wrapper{
		padding: 30px;
	}

	&__row {
		display: flex;
		position: relative;
		justify-content: flex-end;
		height: 387px;
		box-shadow: 0 0 15px 5px #EEEAE4;
		border-radius: 10px;
		transition: all 0.3s ease 0s;
		overflow: hidden;
		&::before{
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 20;
			border-radius: 10px;
			background: rgba(43, 43, 43, 0.4);
		}
	}
	&__left {
		position: absolute;
		z-index: 6;
		left: 0;
		width: 50%;
		height: 100%;
		background: #FCF8F2;
		border-radius: 10px 0 0 10px;
	}

	&__right {
		flex: 0 1 100%;
		text-align: right;
		position: relative;
		z-index: 5;

	}

	&__image {
		height: 100%;
		img{
			max-width: 100%;
			height: 100%;
			border-radius: 10px;
			object-position: right;
		}
		
	}

	&__pagination {
		margin: 65px 0px 0px 0px;
		.swiper-pagination {
			position: relative;
			display: flex;
			justify-content: center;
			gap: 30px;
			span{
				width: 97px;
				height: 5px;
				border-radius: 5px;
				background: #D0CFCF;
			}
		}
		
	}
	.swiper-slide-active {
		transition: all 0.3s ease 0s;
		.main-slider__row{
			height: 561px;
			&::before{
				display: none;
			}
			&::after{
				content: '';
				position: absolute;
				z-index: 10;
				top: 0px;
				left: 54%;
				width: 100%;
				max-width: 285px;
				height: 100%;
				background: url(../img/icon/Vector24.svg) 100% no-repeat;
				background-size: contain;
				@media (max-height: 600px) {
					display: none;
	
				}
			}
		}
		
		.main-slider__left {
			left: 0;
			width: 80%;
			clip-path: polygon(evenodd, 0% 100%, 0% 0%, 94% 0%, 64.1% 47.02%, 63.8% 47.52%, 63.6% 47.8%, 63.3% 48.76%, 63.4% 49.3%, 63.4% 49.5%, 94% 100%);
			@media (max-height: 600px) {
				clip-path: none;
				width: 60%;

			}
		}
		.main-slider__image{
			img{
				width: 50%;
				object-fit: cover;
			}
		}
		
	 }
}
.main-slider-container {
	height: 100%;
	display: flex;
	border-radius: 10px 0 0 10px;
	&__text {
		display: flex;
		flex-direction: column;
		justify-content: center;
		color: $text;
		margin: 0px 0px 0px 39px;
		max-width: 386px;
	}

	&__title {
		font-weight: 700;
		font-size: 50px;
		line-height: 56px;
	}

	&__subtitle {
		font-weight: 700;
		font-size: 23px;
		line-height: 35px;
		letter-spacing: 1px;
		margin: 25px 0;

	}

	&__paragraph {
		font-weight: 500;
		font-size: 16px;
		line-height: 25px;
		color: #686868;
		margin: 0px 0px 72px 0px;
	}
	&__btn{
		
		a{
			border-radius: 6px;
			padding: 13px 79px;
		}
	}
}



