// dealer =========================
.dealer.--margin-block {
	margin: 167px 0px 188px 0px;
}

.dealer-top {
	position: relative;
		&__video {
			position: relative;
			video{
				border-radius: 10px;
				width: 100%;
				height: 100%;
			}
	&::after{
		content: '';
		width: 100%;
		height: 99%;
		position: absolute;
		border-radius: 10px;
		top: 0;
		left: 0;
		background: linear-gradient(270deg, #000000 -56.56%, rgba(0, 0, 0, 0.44) 119.39%);
	}
		}
		&__video-active{
			&::after{
				display: none;
			}
		}
		&__text {
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			color: #fff;
			display: flex;
			width: 100%;
			height: 100%;
			flex-direction: column;
			flex: 1 1 auto;
			justify-content: center;
		}

		&__text-title {
			margin: 0px 0px 48px 0px;
		}

		&__text-subtitle {
			text-align: center;
			font-weight: 700;
			font-size: 23px;
			line-height: 35px;
			letter-spacing: 1px;
			
		}

		&__play {
			text-align: center;
			margin: 95px 0 0 0;
			
		}
		&__circle{
			width: 103px;
			height: 103px;
			border-radius: 50px;
			border: 7.75px solid #FFFFFF;
			margin: 0 auto;
			position: relative;
			cursor: pointer;
			&::after{
				content: '';
				position: absolute;
				z-index: -1;
				width: 110%;
				height: 110%;
				top: 0px;
				left: -10px;
				background: $gradient;
				border-radius: 50%;
				transition: all 0.3s ease 0s;
			}
			&::before{
				content: '';
				border: solid #fff;
				border-width: 0 6px 6px 0;
				width: 33%;
				height: 33%;
				position: absolute;
				top: 30%;
				left: 30%;
				display: block;
				padding: 3px;
				transform: rotate(-45deg);
				transition: all 0.3s ease 0s;
			}
			&:hover{
				&::after{
					left: 0;
					height: 105%;
					width: 105%;
					right: 0;
					top: -2px;
				}
				&::before{
					width: 35%;
					height: 35%;
					left: 27%;
				}
			}
		}
		&__circle-active{
			&::before{
				content: '';
				position: absolute;
				top: 32%;
				left: 38%;
				display: block;
				width: 5px;
				height: 33%;
				background: #fff;
				border: none;
				transform: rotate(0);
				padding: 0;
			}
			span{
				position: absolute;
				display: block;
				top: 32%;
				left: 52%;
				width: 5px;
				height: 33%;
				background: #fff;
			}
			&:hover{

				&::before{
					width: 5px;
					height: 33%;
					left: 38%;
				}
			}
		}
		.--title {
			color: #fff;
			div{
				&::before{
					display: none;
				}
			}

		}
}

.dealer-bottom {
	margin: 76px 0px 0px 0px;
		&__text {
