	top: 0%;
	left: 0;
	max-width: 100%;
	cursor: auto;
	z-index: 4;
	box-shadow: 1px 4px 16px 4px rgba(254, 156, 0, 0.27);
	.reviews{
		&__item-image{
			display: block;
			padding: 50px 80px 0;
		}
		&__item-author {
			margin: 26px 0px 0px 0px;
			padding: 0px 80px 0;
		}
	
		&__item-title {
			font-weight: 700;
			font-size: 23px;
			line-height: 35px;
			margin: 5px 0px 25px;
			max-width: 100%;
			color: $dark-orang;
			padding: 0px 80px 0;
		}
	
		&__item-text {
			background: #E8E8E8;
			border-radius: 10px;
			max-width: 100%;
			padding: 17px 45px;
			margin: 0 80px;
		}
		
	}

}

.reviews-fixed.--gradient-border{
	&::after{
		width: 414px;
		height: 117px;
		border-width: 2px;
	}
}
// Certificates ============
.certificates {
	position: relative;
	margin: 0px 0px 164px 0px;
	&::after{
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 192px;
		background: $bg-cover;
		z-index: 0;
		margin: -20px 0px 0px 0px;
	}
	&__title {
		margin: 0px 0px 76px 0px;
	}

	&__row {
		display: flex;
		position: relative;
		justify-content: space-between;
		z-index: 2;
		gap: 51px;
	}

	&__item {
		border-radius: 18px;
		border: 2px solid #878787;
		background: $bg-cover;
		display: block;
		flex: 0 1 25%;
		padding: 34px;
		transition: all 0.3s ease 0s;
		position: relative;
		max-height: 324px;
		img{
			width: 100%;
		}
		&::after{
			content: 'Все сертификаты';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 0%;
			background: $dark-orang;
			border-radius: 18px;
			text-align: center;
			font-weight: 600;
			font-size: 0px;
			line-height: 35px;
			letter-spacing: 1px;
			color: #fff;
			display: flex;
			align-items: center;
			justify-content: center;	
			transition: all 0.3s ease 0s;
		}
		&:hover{
			&::after{
				height: 100%;
				font-size: 23px;
			}
		}
		
	}
}


// about ======================
.about.--padding-block {
	padding: 101px 0 174px;
}
.about-preview.--gradient-border{
	&:hover{
		&::after{
			width: 570px;
			height: 98px;
		}
	}
}
.about {
	transition: all 0.3s ease 0s;
	&__title {
		margin: 0px 0px 76px 0px;
	}
	
}

.about-preview {
	border-radius: 10px;
	transition: all 0.3s ease 0s;
	&:hover{
		box-shadow: 1px 4px 16px 4px rgba(177, 177, 177, 0.4);
	}
	&__top {
		background: #FCF8F2;
		border-radius: 10px;
	}

	&__top-row {
		display: flex;
		gap: 27px;
		padding: 60px 120px 60px 132px;
	}

	&__top-title {
		font-weight: 700;
		font-size: 23px;
		line-height: 35px;
		text-align: center;
		letter-spacing: 1px;
		flex: 1 1 auto;
		span{
			transition: all 0.3s ease 0s;
		}
	}

	&__top-arrow {
		position: relative;
		z-index: 30;
		img{
			cursor: pointer;
			transition: all 0.3s ease 0s;
		}
	}

	&__top-image {
		img{
			max-width: 100%;
			border-radius: 0px 0px 10px 10px;
			margin: 0 0 -2px;
		}

	}
}
.about-bottom {
	display: none;
}

// about-active
.about-active{
	transition: all 0.3s ease 0s;
	span{
		color: $main-color;
	}
	.--gradient-border{
			&::after{
				display: none;
			}

	}
	

	.about-bottom{
		background: #FCF8F2;
		display: block;

	}
	.about-preview{
		box-shadow: none;
		&__top{
			border-radius: 10px 10px 0 0;

		}
		&__top-arrow{
			img{
				transform: rotate(180deg);
			}
		}
		&__top-image{
			display: none;
			img{
				margin: 0;
			}
		}
	}
}

.about-bottom {

	&__container {

	}
}
.about-swiper {
	display: flex;
	padding: 0px 0px 0px 64px;
	gap: 35px;
	&__text {
		flex: 0 1 50%;
		& p:last-child{
			margin: 10px 0px 0px 0px;
		}
	}
	&__container{
		flex: 0 1 50%;
		display: flex;
		overflow: hidden;
		background: #EAEAEA;
		align-items: center;
		border-radius: 10px 0 0 10px;
		padding: 18px;
	}
	&__swiper-arrow {
		position: relative;
		justify-content: center;
		flex: 0 1 75px;
		.swiper-button-prev{
			&::after{
				transform: rotate(180deg);
				margin: 0 0 0 0px;
			}
		}
		.swiper-button-prev,
		.swiper-button-next{
			position: relative; 
			top: 0;
			left: 0;
			right: 0;
			&::after{
				content: '';
				font-size: auto;
				font-family: inherit;
				
				width: 36px;
				height: 36px;
				display: block;
				background: url(../img/about/arrow-down.svg) 100% no-repeat;
			}

	}
		.arrow-swiper{
			position: relative;
			display: block;
			margin: 0;
			padding: 0;
		}
	}
	&__swiper {
		flex: 0 1 auto;
		height: 230px;

	}
	&__slide {
		img{
			width: 100%;
			height: 230px;
			object-fit: cover;
			border-radius: 8px;
		}
	}
}

.about-services {
	margin: 24px 0px 16px 0px;
	&__title {
		padding: 0px 0px 0px 64px;
	}

	&__text {
		margin: 0px 0px 16px 0px;
		padding: 0px 0px 0px 64px;
	}

	&__list {
		display: flex;
		padding: 20px 0px 20px 64px;
		flex-wrap: wrap;
		justify-content: space-between;
		background: #EAEAEA;
		gap: 10px;
		li{
			flex: 1 1 48%;
			list-style: inside;
			color: $dark-orang;
			list-style-position: outside;
			span{
				color: $text;
			}
		}
	}
}
.about-item {
	padding: 0px 0px 0px 64px;

	&__title {
	}
}
.about-choose {
	margin: 24px 0px 20px 0px;

	&__title {
		padding: 0px 0px 0px 64px;
	}

	&__list {
		padding: 20px 0px 10px 64px;
		background: #EAEAEA;
		gap: 10px;
		li{
			list-style: inside;
			padding: 0px 0px 10px 0px;
			color: $dark-orang;
			list-style-position: outside;
			span{
				color: $text;
			}
		}
	}
}
.about-row {
	display: flex;
	gap: 14px;
	justify-content: space-between;
	align-items: end;
	padding: 0px 0px 0px 64px;
	&__left {
		flex: 1 1 auto;
		padding: 0px 0px 60px 0px;
	}
	&__title-2 {
		margin: 24px 0px 0px 0px;
	}

	&__right {
		flex: 0 0 286px;
		text-align: center;
		background: #EAEAEA;
		padding: 70px 0px;
		border-radius: 10px 0 0 0;
	}

	&__right-btn {
		a{
			background: $dark-orang;
			&:hover{
				background: #de8901;
			}
		}
	}
}



// footer ==========================

.footer {
	margin: 164px 0px 0px 0px;
	padding: 109px 0px 130px 0px;
	background: $bg-cover;
	&__row {
		display: flex;
		justify-content: space-between;
		gap: 76px;
	}

	&__item {
		flex: 0 1 33%;
	}

	&__item-title {
		margin: 0px 0px 43px 0px;
		font-weight: 700;
		font-size: 30px;
		line-height: 45px;
		border-bottom: 2px solid #878787;
		padding: 0px 0px 15px 0px;
		color: $text;
	}

	&__item-list {
		li{
			margin: 0px 0px 20px 0px;
			position: relative;
			
			&::after{
				content: '';
				position: absolute;
				bottom: -4px;
				width: 43px;
				height: 2px;
				left: 0;
				background: #878787;
				transition: all 0.3s ease 0s;
			}
			a{
				color: $text;
				font-weight: 600;
				font-size: 16px;
				line-height: 25px;
				transition: all 0.3s ease 0s;
				&:hover{
					color: $main-color;
				}
			}
			&:hover{
				&::after{
					background: $main-color;
				}
			}
			
		
		}
	}

	&__item-select {
		display: flex;
		align-items: center;
		font-weight: 600;
		gap: 5px;
		transition: all 0.3s ease 0s;
		&:hover{
			color: $main-color;
			select{
				color: $main-color;
			}
		}
		select{
			background: $bg-cover;
			font-weight: 600;
			border: none;
			width: 100%;
			max-width: 130px;
			cursor: pointer;
			transition: all 0.3s ease 0s;
		}

	}
	&__item-social{
		li{
			&::after{
				display: none;
			}
			a{
				display: flex;
				align-items: center;
				gap: 10px;
			}
		}
	}
}
