.container {
	width: 100%;
	max-width: 1145px;
	padding: 0 15px;
	margin: 0 auto;
	@media (max-width: 1145px) {
		max-width: 1065px;
	}
	@media (max-width: 992px) {
		max-width: 960px;
	}
	@media (max-width: 768px) {
		max-width: 720px;
	}
	@media (max-width: 576px) {
		max-width: 540px;
	}
}

.--btn{
	a{
		font-weight: 800;
		font-size: 17px;
		line-height: 21px;
		background: $gradient;
		padding: 10px 22px;
		display: inline-block;
		border-radius: 7px;
		color: #fff;
		transition: all 0.3s ease 0s;
		position: relative;
		&:hover{
			background: linear-gradient(100deg, #FE9C00 1.85%, #F56900 88.68%);
		}
	}
}
.--margin-block{
	margin: 206px 0px 168px 0px;
}
.--padding-block {
	padding: 135px 0 183px;
	background: $bg-cover;
}
.--title {
	font-weight: 800;
	font-size: 55px;
	line-height: 45px;
	color: $text;
	text-align: center;
	position: relative;
	z-index: 3;
	div{
		display: inline-block;
		position: relative;
		&::before{
			content: '';
			position: absolute;
			left: -20px;
			top: 0;
			height: 100%;
			border-radius: 5px;
			width: 71px;
			background: $dark-orang;
			z-index: -1;
			clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
	
		}
	}
	
}


.--gradient-border{
	position: relative;
	&::after{
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 0;
		height: 0;
		border: 0px solid $dark-orang;
		border-radius: 0 10px 0 0;
		border-bottom: none;
		transition: all 0.3s ease 0s;
		border-left: none;
		border-image: repeating-linear-gradient(251.59deg, #FE9C00 24.85%, #F56900 213.68%) 2px;
	}
	&:hover{
		&::after{
			width: 414px;
			height: 117px;
			border-width: 2px;
		}
	}
}

.--text{
	font-weight: 500;
	font-size: 16px;
	line-height: 23px;
	letter-spacing: 1px;
	color: $text;
	margin: 8px 0px 0px 0px;
}
.--minititle{
	font-weight: 600;
	font-size: 19px;
	line-height: 35px;
	letter-spacing: 1px;
	color: $text;
}