*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
	background: #FCF8F2;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	list-style: none;
	padding: 0;
}
a{
	text-decoration: none;
}

body.lock{
	overflow: hidden;
}

.wrapper{
	display: flex;
	flex-direction: column;
	min-height: 100%;
}
.content{
	flex: 1 1 auto;
}